.App{text-align:center}*{margin:0;padding:0;list-style:none;vertical-align:baseline}a{font-weight:500;text-decoration:none;color:inherit}div{position:relative;z-index:2}body{background-color:#242320}#wrapper{transition:all linear 2.5s;background-color:#242320;background-size:cover;color:#333;font-family:"Montserrat", sans-serif;font-weight:380;display:flex;justify-content:center;align-items:center;height:100vh;width:100vw}.footer{width:100%;text-align:center;display:block;position:absolute;margin:80vh auto 2vh auto;font-size:0.9em;color:#fff}.footer button{font-family:"Dosis", sans-serif;padding:0.5em;font-size:1.2em;background-color:transparent;font-weight:100;font-size:1.3em;border:2px solid;border-color:#fff;border-radius:10px;color:#fff}.footer button:hover{background-color:#f39c12;font-weight:200;border:none;color:#333;cursor:pointer;transform:scale(1.1);box-shadow:0 0 10px #f39c12, 0 0 40px #f39c12, 0 0 80px #f39c12}#quote-box{border-radius:3px;position:relative;margin-top:0vh;margin-left:60vw;margin-bottom:50vh;width:20vw;padding:3vh 2vw;display:table;background-color:#242320}#quote-box .quote-text{text-align:center;width:inherit;height:auto;clear:both;font-weight:500;font-size:1.3em}#quote-box .quote-text i{font-size:1em;margin-right:0.4em}#quote-box .quote-author{width:inherit;height:auto;clear:both;padding-top:20px;font-family:"Tulpen One", cursive;font-size:1.6em;text-align:right}#quote-box .buttons{width:auto;margin:auto;display:block}#quote-box .buttons .button{height:38px;border:none;border-radius:3px;color:#242320;background-color:#333;outline:none;font-size:0.85em;padding:8px 18px 6px 18px;margin-top:30px;opacity:1;cursor:pointer}#quote-box .buttons .button:hover{opacity:0.9;color:#fff}#quote-box .buttons .button#tweet-quote{float:left;padding:0px;padding-top:8px;text-align:center;font-size:1.2em;margin-right:5px;width:40px}#quote-box .buttons .button#new-quote{float:right}@media only screen and (max-width: 991px){#quote-box{width:70vw;margin:0;margin-top:1px;margin-left:0;margin-bottom:0}.buttons{width:auto}.footer{margin:50vh auto 5vh auto}}

